import { useState } from "react";
import "./App.css";
import {
  Box,
  Button,
  FormControlLabel,
  Grid,
  Radio,
  RadioGroup,
  TextField,
  Typography,
} from "@mui/material";
import { getAssisstance } from "./api";

const defaultCommunityGuidelines = `No hate speech, threats.
No personal details exchange before service booking.
No start task before booking.
No suspicious or illegal activity.
No cash payments allowed.
No irrelevant information.`;
const situations = [
  "Before booking: Title for job",
  "Before booking: Details for job",
  "Before booking: Question and answers about job post",
  "Before booking: Chat",
  "After service booked: Chat ",
  "After job completion: Feedback about the job",
];

function Chat() {
  const [guidelines, setGuidelines] = useState(defaultCommunityGuidelines);
  const [situation, setSituation] = useState("");
  const [userInput, setUserInput] = useState("");

  const [result, setResult] = useState<null | {
    result: boolean;
    reason: string;
  }>(null);
  const clickHandler = () => {
    getAssisstance({ guidelines, situation, userInput }).then((res) => {
      res
        .json()
        .then((data) => setResult(JSON.parse(data.choices[0].message.content)));
    });
  };

  return (
    <Box component="form" maxWidth={800} margin="auto">
      <Typography variant="h2" gutterBottom>
        Chat GPT Integration
      </Typography>
      <Grid sx={{ flexGrow: 1 }} container spacing={2}>
        <Grid item md={12}>
          <Typography variant="h6" textAlign="left" gutterBottom>
            Situation
          </Typography>
          <RadioGroup
            onChange={(e) => setSituation(e.target.value)}
            defaultValue={situations[0]}
          >
            {situations.map((situation) => (
              <FormControlLabel
                key={situation}
                value={situation}
                control={<Radio />}
                label={situation}
              />
            ))}
          </RadioGroup>
        </Grid>
        <Grid item md={12}>
          <TextField
            fullWidth
            value={userInput}
            onChange={(e) => setUserInput(e.target.value)}
            label="User Input"
            multiline
            variant="filled"
          />
        </Grid>
        <Grid item md={12}>
          <TextField
            fullWidth
            label="Community Guidelines"
            multiline
            variant="filled"
            value={guidelines}
            onChange={(e) => setGuidelines(e.target.value)}
          />
        </Grid>
        <Grid item md={12}>
          <Button fullWidth variant="contained" onClick={clickHandler}>
            Validate Community Guidelines
          </Button>
        </Grid>
        {result && (
          <Grid item>
            <Typography variant="h6" textAlign="left" gutterBottom>
              Result
            </Typography>
            <div style={{ textAlign: "left" }}>
              <b>Following guidelines:</b> {result.result ? "Yes" : "No"}
            </div>
            <div style={{ textAlign: "left" }}>
              <b>Reason:</b> {result.reason}
            </div>
          </Grid>
        )}
      </Grid>
    </Box>
  );
}

export default Chat;
