import CryptoJS from "crypto-js";

export const getAssisstance = ({
  guidelines,
  situation,
  userInput,
}: {
  guidelines: string;
  situation: string;
  userInput: string;
}) =>
  fetch("https://api.openai.com/v1/chat/completions", {
    body: JSON.stringify({
      model: "gpt-3.5-turbo-1106",
      response_format: { type: "json_object" },
      messages: [
        {
          role: "system",
          content: "You are a helpful assistant designed to output JSON.",
        },
        {
          role: "user",
          content: `Our platform facilitates users in booking services provided by others, 
          and communication is permitted following strict community guidelines. 
          The guidelines include ${guidelines}. 
          Text: ${userInput}.
          Situatin: ${situation}.
          return json in format {result: true or false, reason: as string}`,
        },
      ],
    }),
    headers: {
      Authorization: `Bearer ${CryptoJS.AES.decrypt(
        "U2FsdGVkX18W2nJtqVMjkmqOKb/14hFyqCdUMp1l0vl/d1bBeNx8FW64kPYk6AYn66NfCmQFOmzJvZN3jEoQK0Zr6wIOqusqh93gMlTW5bk=",
        "12345"
      ).toString(CryptoJS.enc.Utf8)}`,
      "Content-Type": "application/json",
    },
    method: "POST",
  });
